const slideInTop =(elem, delay, duration) =>{
  gsap.fromTo(
    elem,
    {
      opacity:0,
      y:200
    },
    {
      opacity:1,
      y:0,
      delay : 0.6,
      duration : 1,
        scrollTrigger:{
        trigger : elem,
        start:"top center",
        end:"bottom center"
      }

    }
  )
}

const slideOpacity =(elem, delay, duration) =>{
  gsap.fromTo(
    elem,
    {
      opacity:0
    },
    {
      opacity:1,
      delay : 0.6,
      duration : 1,
        scrollTrigger:{
        trigger : elem,
        start:"top center",
        end:"bottom center"
      }

    }
  )
}



const slideInLeft =(elem, delay, duration) =>{
  gsap.fromTo(
    elem,
    {
      opacity:0,
      x:-200
    },
    {
      opacity:1,
      x:0,
      delay : delay || 0.4,
      duration : duration || 0.6,
      scrollTrigger:{
        trigger : elem,
        start:"top center",
        end:"bottom center"
      }

    }
  )
}


